<template>
  <div>
    <v-back></v-back>
    <v-search></v-search>
    <hr>
    <ul>
      <li class="item" v-for="item in foods" :key="item.foodId" @click="toFoodDetail(item.foodId)">
        <p>名称：{{item.name}}</p>
        <p>价格：{{item.price | priceFilter}}</p>
      </li>
    </ul>
  </div>
</template>
<script>
  export default {
    // beforeRouteEnter()进来之前
    beforeRouteEnter (to, from, next) {
      // 非常注意：这里面取不到this，因为beforeRouteEnter比组件进来的还要早
      console.log(this);    //undefined
      if(from.path=="/index/home"||from.path.includes("/foodDetail")){
        next()
      }else{
        next("/index/home")
      }
    },
    // beforeRouteLeave()离开之前
    beforeRouteLeave(to,form,next){
      // 可以控制是否让他离开
      next()
      console.log("外卖小哥离开了");
    },

    data() {
      return {
        foods: [{
            foodId: 1,
            name: '麻辣烫',
            price: 15.50
          },
          {
            foodId: 2,
            name: '火锅',
            price: 85.00
          },
          {
            foodId: 3,
            name: '海鲜自助',
            price: 234.50
          }
        ]
      }
    },
    methods: {
      toFoodDetail(id) {
        this.$router.push("/foodDetail/" + id)
      }
    },
    watch: {},
    mounted() {}
  }

</script>
<style scoped>
  .item {
    width: 80%;
    margin: 15px auto;
    padding: 15px;
    background: pink;
    display: block;
  }

</style>



